body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  text-align: center;
  min-width: 600px;
  overflow: auto; }
  .app__header {
    background-color: #282c34;
    height: 20vmin;
    width: 100vw;
    min-width: 600px;
    overflow: auto;
    display: table-cell;
    vertical-align: bottom;
    font-size: calc(10px + 2vmin);
    color: white; }
  .app__link-container {
    height: 1.7em; }
  .app__link {
    color: white;
    text-transform: uppercase;
    text-decoration: none;
    padding-left: 1.2ch;
    padding-right: 1.2ch; }
    .app__link::before {
      content: "[ "; }
    .app__link::after {
      content: " ]"; }

.user-groups {
  list-style: none;
  grid-column-gap: 2em;
  -webkit-column-gap: 2em;
          column-gap: 2em;
  -webkit-columns: 5;
          columns: 5;
  -webkit-column-fill: balance;
          column-fill: balance;
  margin: 0;
  padding: 40px;
  margin-bottom: 40px; }
  .user-groups__group {
    display: flex;
    flex-direction: row;
    width: 200px;
    height: 300px;
    background-color: aliceblue;
    margin-bottom: 2em; }
    .user-groups__group:last-of-type {
      margin-bottom: 0; }

.users-table {
  display: table;
  width: 100%; }
  .users-table__row {
    display: table-row;
    background-color: white; }
    .users-table__row:nth-child(2n) {
      background-color: whitesmoke; }
    .users-table__row_unmanaged {
      background-color: aliceblue; }
      .users-table__row_unmanaged:nth-child(2n) {
        background-color: #dfe8f0; }
  .users-table__cell {
    display: table-cell; }

.user-tiles {
  display: flex;
  flex-flow: row wrap;
  list-style: none;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
  -webkit-padding-end: 0;
          padding-inline-end: 0;
  padding-left: 40px;
  padding-right: 40px;
  -webkit-margin-before: 0;
          margin-block-start: 0; }
  .user-tiles__tile {
    position: relative;
    display: flex;
    flex-basis: 180px;
    flex-grow: 1;
    flex-direction: column;
    width: 180px;
    height: 250px;
    margin: 1em; }
  .user-tiles__placeholder {
    content: "";
    display: flex;
    flex-basis: 180px;
    flex-grow: 1;
    width: 180px;
    height: 0;
    margin-left: 1em;
    margin-right: 1em; }

.user-tile {
  background-color: #f3f3f3;
  text-align: center;
  font-size: 14px; }
  .user-tile > * {
    padding: 0; }
  .user-tile__name {
    padding-top: 38px;
    font-weight: bold;
    margin-top: 6px;
    margin-bottom: 6px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    width: 180px;
    margin-left: auto;
    margin-right: auto; }
  .user-tile__picture {
    width: 90px;
    height: 90px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 12px;
    margin-bottom: 12px;
    overflow: hidden; }
    .user-tile__picture:before {
      content: " ";
      box-sizing: border-box;
      display: block;
      width: 90px;
      height: 90px;
      background-color: #e6e6e6;
      border: 1px solid #c8c8c8; }
  .user-tile__phone {
    margin-top: 3px;
    margin-bottom: 3px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    width: 180px;
    margin-left: auto;
    margin-right: auto; }
  .user-tile__group {
    margin-top: 3px;
    margin-bottom: 3px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    width: 180px;
    margin-left: auto;
    margin-right: auto; }
    .user-tile__group_unmanaged {
      font-weight: bold; }
  .user-tile__check {
    position: absolute;
    top: 10px;
    left: 10px; }
  .user-tile__upload-button {
    width: 28px;
    height: 28px;
    outline: none;
    border: none;
    background-repeat: no-repeat;
    background-size: contain;
    background-image: url(/kl-task/static/media/cloud-upload.da552689.svg);
    background-color: transparent;
    display: inline-block;
    cursor: pointer;
    position: absolute;
    top: 10px;
    right: 15px; }

.switcher {
  display: inline-block;
  justify-content: flex-start;
  padding-top: 3px; }
  .switcher > * {
    margin: 0 8px 0 8px; }
  .switcher__button {
    width: 32px;
    height: 32px;
    background-repeat: no-repeat;
    background-size: contain;
    display: inline-block;
    cursor: pointer;
    opacity: 0.2; }
    .switcher__button:hover {
      opacity: 0.6; }
    .switcher__button_active {
      opacity: 1; }
      .switcher__button_active:hover {
        opacity: 1; }
    .switcher__button_type_tile {
      background-image: url(/kl-task/static/media/tile-view.c0e4a0b3.svg); }
    .switcher__button_type_group {
      background-image: url(/kl-task/static/media/group-view.f49be5eb.svg); }
    .switcher__button_type_table {
      background-image: url(/kl-task/static/media/table-view.54f8aa47.svg); }

.search {
  display: inline-block;
  align-items: baseline; }
  .search__input, .search__label {
    font-size: calc(10px + 1vmin); }
  .search__label {
    padding-right: 1ch;
    color: #575757; }
  .search__input {
    outline: none;
    border: 2px solid lightgray;
    padding-left: 0.5ch;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif; }

.users-container__header-panel {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center; }
  .users-container__header-panel > * {
    margin: 30px; }

.users-container__content-panel {
  display: flex;
  flex-direction: row;
  justify-content: center; }

